<template>
  <nav class="nav" v-if="!nav_state" @mouseover="nav_state = true">
    <v-row class="nav_con">
      <v-col class="nav_logo" cols="4">
        <img
          src="../../assets/logo/whitelogo.svg"
          alt="logo"
          class="blogo"
          style=""
        />
      </v-col>
      <v-spacer />
      <v-col cols="8" class="menu_con">
        <div class="menu_list">
          <template v-for="(item, i) in menus" :key="i">
            <router-link :to="item.value">
              <v-list-item :value="i">
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item>
            </router-link>
          </template>
        </div>
      </v-col>
      <v-col cols="8" class="small_nav">
        <div class="nav_btn">
          <v-icon>mdi-menu</v-icon>
        </div>
      </v-col>
    </v-row>
  </nav>
  <ScrollNav v-if="nav_state" @mouseleave="mouseleaveheader()" />
</template>

<script>
import { reactive } from "vue";
import ScrollNav from "../header/ScrollNav.vue";

export default {
  name: "HeaderNav",
  components: {
    ScrollNav,
  },

  setup() {
    const state = reactive({
      nav_state: false,
      menus: [
        {
          title: "회사소개",
          value: "/Intro",
        },
        {
          title: "회사소식",
          value: "",
        },
        {
          title: "서비스",
          value: "",
        },
        {
          title: "문의&의뢰",
          value: "",
        },
        {
          title: "채용",
          value: "",
        },
      ],
    });
    window.addEventListener("wheel", (e) => {
      if (window.innerHeight > window.scrollY) {
        state.nav_state = false;
      }
      if (e.wheelDeltaY < 0) {
        state.nav_state = true;
      }
    });

    return state;
  },
  mounted() {
    this.$anime({
      targets: [".nav"],
      top: "0px",
      duration: 200,
      easing: "easeInSine",
    });
  },
  methods: {
    mouseleaveheader() {
      if (window.scrollY === 0) {
        this.nav_state = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@media screen and (min-width: 1080px) {
}
// 노트북
@media screen and (min-width: 760px) and (max-width: 1080px) {
  .menu_con {
    display: flex;
  }
}
// 태블릿
@media screen and (min-width: 640px) and (max-width: 759px) {
  .menu_con {
    display: flex;
  }
}
// 휴대폰
@media screen and (min-width: 320px) and (max-width: 639px) {
  .small_nav {
    display: flex;
  }
  .menu_con {
    display: none;
  }
}

.nav {
  z-index: 999;
  position: fixed;
  height: 80px;
  width: 100vw;
  top: 0;
  // box-shadow: 1px 0px 5px 0px gray;
}
.small_nav {
  display: none;
  justify-content: right;
  align-items: center;
  width: 100%;
  height: 100%;
  .nav_btn {
    font-size: 2rem;
    color: white;
    padding: 16px;
  }
}
.nav_con {
  position: relative;
  height: 100%;
  width: 68vw;
  margin: auto;
  .nav_logo {
    align-self: center;
    cursor: pointer;
    top: 0;
    .blogo {
      width: 158px;
      height: 38px;
    }
  }
}

.menu_con {
  align-self: center;
  .menu_list {
    position: relative;
    display: flex;
    justify-content: flex-end;
  }
  .menu_list a {
    text-decoration: none;
  }

  .menu_list .v-list-item-title {
    font-family: "Pretendard-Regular";
    font-size: 1.125rem;
    color: rgb(255, 255, 255);
  }
}
// 노트북
@media screen and(min-width: 760px) and(max-width:1080px) {
  .nav_con {
    width: 100vw;
  }
  .small_nav {
    display: none;
  }
  .menu_con {
    display: flex;
    justify-content: right;
    .menu_list {
      padding-right: 16px;
    }
  }
}
// 태블릿 & 핸드폰
@media screen and(min-width: 360px) and(max-width:760px) {
  .nav_con {
    width: 100vw;
  }
  .small_nav {
    display: flex;
  }
  .menu_con {
    display: none;
  }
}
</style>
